import { VALIDATORS } from "./constants";

export const Validate = (schema, data) => {
  const errors = new Map();
  for (const item of schema) {
    switch (item.validator) {
      case VALIDATORS.REQUIRED:
        if (!data[item?.name]) errors.set(item.name, "This field is required");
        break;
      case VALIDATORS.NUMBER:
        if (typeof data[item?.name] !== "number")
          errors.set(item.name, "Please enter a valid value!");
        break;
      default:
    }
  }
  return errors;
};

export const createEvent = (name, value) => ({
  target: {
    name,
    value,
  },
});