import React from "react";
import styles from "./input.module.scss";
import cx from "classnames";

const InputDate = ({
  value,
  name,
  placeholder,
  onChange,
  top,
  error,
  height,
  width,
  title,
  classes,
  required,
  icon,
  gray,
  preInput,
  iconLeft,
  disabled,
  type,
  onFocus
}) => {
  return (
    <div
      data-testid="input"
      style={{ marginTop: top,width:'100%' }}
      className={cx(styles.inputContainer, classes)}
    >
      {!!title && (
        <p className={styles.title}>
          {title}
          {!!required && <span>*</span>}
        </p>
      )}
      {!!preInput ? (
        <div style={{ width: !!width && width }} className="flex items-center">
          <div className={cx(styles.preInput, styles.input, "mr-1")}>
            {preInput}
          </div>
          <input
            name={name}
            type='date'
            style={{ height: !!height && height, width: "100%" }}
            className={cx(
              styles.input,
              { [styles.gray]: gray },
              { [styles.error]: !!error }
            )}
            placeholder={placeholder}
            value={value}
            onChange={onChange}
            disabled={disabled}
            onFocus={onFocus}
          />
        </div>
      ) : (
        <input
          name={name}
          type='date'
          style={{
            height: !!height && height,
            width: !!width && width,
            paddingLeft: !!iconLeft && "30px",
            paddingRight: (!!icon && !iconLeft) && "28px",
           border:'1px solid black'
          }}
          className={cx(
            styles.input,
            { [styles.gray]: gray },
            { [styles.error]: !!error }
          )}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          disabled={disabled}
          onFocus={onFocus}
        />
      )}
      {error && <p className={styles.error}>{error}</p>}
      <span
       style={{ top: title && "60%" }}
        className={`${
          !!icon
            ? cx(styles.icon, { [styles.left]: iconLeft })
            : "display-none"
        }`}
      >
        {!!icon && icon}
      </span>
    </div>
  );
};

export default InputDate;