import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useAsset = () => {
  // const deviceStateId = useSelector((state) => state?.DeviceId?.data)
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const deviceId = localStorage.getItem("deviceId")
  

  const headers = {
    Authorization: `Bearer ${token || localStorage.getItem("t_token")}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async () => {
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.TENANT}/${ENDPOINTS.DEVICES}?pageSize=999999999&page=0&type=${ENDPOINTS.DGPV}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data.data)
      return [res?.data.data];
    }
    return [[], 0];
  };

  const assetsList = async () => {
    // console.log("redux assetsList id",deviceStateId,"local",deviceId)
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.PLUGINS}/${ENDPOINTS.TELEMETRY}/DEVICE/${deviceId}/${ENDPOINTS.VALUES}/${ENDPOINTS.ATTRIBUTES}/${ENDPOINTS.SHARED_SCOPE}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      // console.log("res  ",res.data.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const clientAttribute = async () => {
    // console.log("redux clientAttribute id",deviceStateId,"local",deviceId)
    const client = new REQUEST();
    const res = await client.get(
      `${ENDPOINTS.PLUGINS}/${ENDPOINTS.TELEMETRY}/DEVICE/${deviceId}/${ENDPOINTS.VALUES}/${ENDPOINTS.ATTRIBUTES}/${ENDPOINTS.CLIENT_SCOPE}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      // console.log("res  ",res.data.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.PLUGINS}/${ENDPOINTS.TELEMETRY}/DEVICE/${deviceId}/${ENDPOINTS.ATTRIBUTES}/${ENDPOINTS.SHARED_SCOPE}`,
      payload,
      headers,
      (res) => resolveResponse(res, null, `File Successfully Upload in ${ENDPOINTS.SHARED_SCOPE}!`)
    );
    return res;
  }

  const update = async (payload) => {
    const newPayload ={
      "name":payload.name,
      "metadata":payload.metadata
    }
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.ASSET}/${payload.id}/`,
      newPayload,
      headers,
      (res) => resolveResponse(res, null, "Asset Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.ASSET}/${id}/`,
      headers,
      (res) => resolveResponse(res, null, "Asset deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    create,
    update,
    remove,
    assetsList,
    clientAttribute
  };
};

export default useAsset;
