import React, { useState } from "react";
import styles from "./input.module.scss";
import cx from "classnames";
import RemoveIcon from '@mui/icons-material/Remove';
import AddIcon from '@mui/icons-material/Add';

const Counter =({
    value,
    name,
    placeholder,
    onChange,
    top,
    error,
    height,
    width,
    title,
    classes,
    required,
    icon,
    gray,
    preInput,
    iconLeft,
    disabled,
    type,
    onFocus,
    handleCounter
})=>{
    return(
    <div
      data-testid="input"
      style={{ marginTop: top,width:'100%' }}
      className={cx(styles.inputContainer, classes)}
    >
      {!!title && (
        <p className={styles.title}>
          {title}
          {!!required && <span>*</span>}
        </p>
      )}
        <div style={{ width: !!width && width }} className="flex items-center gap-10">
            <RemoveIcon fontSize="small" sx={{cursor:'pointer'}} onClick={()=>handleCounter('sub')}/>
          <input
            name={name}
            type="number"
            style={{ height: !!height && height, width: "15%" }}
            className={cx(
              styles.input,
              { [styles.gray]: gray },
              { [styles.error]: !!error }
            )}
            placeholder={placeholder}
            value={value}
            onChange={onChange}
            disabled={disabled}
            onFocus={onFocus}
          />
          <AddIcon fontSize="small" sx={{cursor:'pointer'}} onClick={()=>handleCounter('add')}/>
        </div>
        </div>
    )
}
export default Counter;



