import React from "react";
import { SCHEMA_TYPES } from "../../helpers/constants";
import Input from "../input";
import InputDate from "../input/indexDate";
import Select from "../input/Select";
import Counter from "../input/Counter";
import SearchableSelect from "../input/SearchableSelect";

const SchemaExtractor = ({ schema, onChange, errors, data,handleCounter}) => {
  return schema.map((d) => {
    switch (d.type) {
      case SCHEMA_TYPES.INPUT:
        return (
          <Input
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            width="99%"
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
            type={d?.inputType}
          />
        );
      case SCHEMA_TYPES.DATE:
        return (
          <InputDate
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            width="99%"
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
          />
        );
      case SCHEMA_TYPES.SELECT:
        return (
          <Select
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            name={d?.name}
            width="100%"
            onChange={onChange}
            error={errors.get(d?.name)}
            value={data[d?.name]}
            values={d?.values}
            keyValues={d?.keyValues}
            required={!!d?.validator}
          />
        );
      case SCHEMA_TYPES.MASTER:
        return (
          <SearchableSelect
            {...d}
            key={d?.name}
            title={d?.title}
            placeholder={d?.placeholder}
            name={d?.name}
            width="99%"
            onChange={onChange}
            error={errors.get(d?.name)}
            value={data[d?.name]}
            values={d?.values}
            fetch={d?.fetch}
            required={!!d?.validator}
          />
        );
        case SCHEMA_TYPES.COUNTER:
          return(
            <Counter
            key={d?.name}
            title={d?.title}
            // placeholder={d?.placeholder}
            width="50%"
            name={d?.name}
            onChange={onChange}
            value={data[d?.name]}
            error={errors.get(d?.name)}
            required={!!d?.validator}
            handleCounter={handleCounter}
            />
          )
      default:
        return null;
    }
  });
};

export default SchemaExtractor;
