import {
  Box,
  Drawer as MuiDrawer,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Icon,
  AppBar,
  Toolbar,
  Avatar,
  Stack,
  Menu,
  MenuItem,
  Button
} from "@mui/material";
import React, { useEffect, useState,useLayoutEffect } from "react";
import Divider from '@mui/material/Divider';
import { useSelector,useDispatch } from "react-redux";
import {
  ChevronLeft,
  ChevronRight,
  Construction,
  Home,
  Logout,
  PeopleOutline,
  Settings,
  Widgets,
  ShoppingCartOutlined,
  email,
} from "@mui/icons-material";
import { Search } from "@mui/icons-material";
import PersonIcon from '@mui/icons-material/Person';
import EmailIcon from '@mui/icons-material/Email';
import InventoryIcon from '@mui/icons-material/Inventory';
import PrecisionManufacturingIcon from '@mui/icons-material/PrecisionManufacturing';
import TimelineIcon from '@mui/icons-material/Timeline';
import LogoutIcon from '@mui/icons-material/Logout';
import styled from "@emotion/styled";
import { useLocation, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import Routes from "../../routes";
import useAuth from '../../store/auth'
import { REDUCER_TYPES } from "../../store";
import {storeUser} from "../../store/api/utils"
import {ROLE} from '../../helpers/constants'
import useAsset from "../../store/asset";
import Input from "../../components/input";
import {setId} from "../../store/asset/itemReducer"


const drawerWidth = 180;

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ open }) => ({
  width: drawerWidth,
  flexShrink: 0,
  whiteSpace: "nowrap",
  boxSizing: "border-box",
  ...(open && {
    width: drawerWidth,
    overflowX: "hidden",
    "& .MuiDrawer-paper": {
      width: drawerWidth,
      overflowX: "hidden",
    },
  }),
  ...(!open && {
    overflowX: "hidden",
    width: "50px",
    "& .MuiDrawer-paper": {
      overflowX: "hidden",
      width: "50px",
    },
  }),
}));

const menu = [
{
  title: "Assests",
  icon: PrecisionManufacturingIcon,
  path: "/config-machine",
},

]
const NavigationLayout = () => {
  const dispatch = useDispatch()
  const [menu_items,setMenu_items] = useState([])
  const [selectedDevice,setSelectedDevice] = useState('')
  const isLoggedIn = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const user = useSelector((state) => state[REDUCER_TYPES.AUTH].user?.data)
  const {logout,profile2} = useAuth()
  const {list} = useAsset()
  const [open, setOpen] = useState(true);
  const navigate = useNavigate();
  const { pathname } = useLocation();
  const toggle = () => setOpen((val) => !val);
  const [anchorEl, setAnchorEl] = useState(null);
  const openpop = Boolean(anchorEl);
  const [allDevices, setAllDevices] = useState([])
  const [searchInput,setSearchInput] = useState('')

  // useEffect(()=>{ 
  //   const sideMenu = []
  //   console.log("redux user",user?.role)
  //   if(user?.role === ROLE.SUPERADMIN ||localStorage.getItem('role') === ROLE.SUPERADMIN){
  //     sideMenu.push(...menu)
  //     setMenu_items(sideMenu)
  //   }else if(user?.role === ROLE.ADMINISTRATOR ||localStorage.getItem('role') === ROLE.ADMINISTRATOR){
  //     sideMenu.push(...menu)
  //     setMenu_items(sideMenu)
  //   }else{
  //     setMenu_items(menu)
  //   }
  //   if(!localStorage.getItem('t_token')){
  //     console.log("islogin",user)
  //   // toast.warn("Please Login!")
  //     navigate('/login',{replace:true})
  //   }
  // },[])

  useLayoutEffect( ()=>{
  fetchDevice()
  },[])

  async function fetchDevice(){
   const tempId= localStorage.getItem("deviceName")
   setSelectedDevice(tempId)
    const deviceList = await list()
    if(!tempId){
      // dispatch(setId(deviceList[0][0].id.id))
     setSelectedDevice(deviceList[0][0].name)
     localStorage.setItem("deviceName",deviceList[0][0].name);
     localStorage.setItem("deviceId",deviceList[0][0].id.id)
    }
    setMenu_items(deviceList[0]?deviceList[0]:[])
    setAllDevices(deviceList[0]?deviceList[0]:[])
  }
  
  const handleClick = (event) => {
    console.log("popup",event.currentTarget)
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  async function handleSearch(val){
    console.log("all devices",allDevices)
    console.log("val",val)
    const filterData = allDevices.filter(device =>
      device.name.toLowerCase().includes(val.toLowerCase())
    );
    setMenu_items(filterData)
    // const filterData = allDevices.find(item => val == item.name);
    console.log("filter devices",filterData)
  }

  return (
   
    <>
    {/* {isLoggedIn &&( */}
     <div>
      <Drawer open={open} variant="permanent" onClose={toggle} elevation={0}>
        <Box className="flex items-center justify-around mt-2">
          <h3 className={`${!open ? "display-none" : "ml-2"}`}>DG Config</h3>
          <IconButton onClick={toggle}>
            {open ? <ChevronRight /> : <ChevronLeft />}
          </IconButton>
        </Box>
        <Box sx={{ width: drawerWidth }} role="presentation">
          <Stack sx={{px:1}}>
        <Input
        width="160px"
            height="37px"
            placeholder="Search Device"
            onChange={(e)=>{setSearchInput(e.target.value);handleSearch(e.target.value)}}
            // icon={<Search sx={{cursor:'pointer'}} />}
          /></Stack>
         <List dense>
            {menu_items.length>0 ? menu_items.map(( item,index) => (
              <ListItem
                key={index}
                className={`py-1 ${selectedDevice === item.name && "bg-opaque"}`}
                onClick={() =>{
                  setSelectedDevice(item.name);
                   localStorage.setItem("deviceName",item.name);
                   localStorage.setItem("deviceId",item.id.id)
                  }}
              >
                <ListItemText
                // color="red"
                  primary={`${index+1}. ${item.name}`}
                  className={`fw-500 ${selectedDevice === item.name && "c-primary"}`}
                />
              </ListItem>
            )):(
            <ListItem>
              <ListItemIcon>
                {/* <Home/> */}
                <ListItemText primary='No Device Found'/>
              </ListItemIcon>
            </ListItem>
            )}
          </List>     
        </Box>
      </Drawer>
      <Box
        sx={{
          position: "relative",
          left: open ? drawerWidth : "50px",
          overflow: "hidden",
        }}
      >
        <AppBar position="static" color="default" variant="outlined">
          <Toolbar
            sx={{ maxWidth: `calc(100vw - ${open ? 180 : 50}px)` }}
            disableGutters
            className="h-50 minh-50 w-full"
          >
            <Stack
              direction="row"
              alignItems="center"
              justifyContent="space-between"
              className="w-full px-3"
            >
              <Box></Box>
              <div>
              <Button
                id="basic-button"
                aria-controls={openpop ? 'basic-menu' : undefined}
                aria-haspopup="true"
                aria-expanded={openpop ? 'true' : undefined}
                onClick={handleClick}
              >
              <Avatar
                src="https://www.svgrepo.com/show/382099/female-avatar-girl-face-woman-user-2.svg"
                sx={{ height: "38px", width: "38px" }}
              /> </Button>
              <Menu
              
                id="basic-menu"
                anchorEl={anchorEl}
                open={openpop}
                onClose={handleClose}
                MenuListProps={{
                  'aria-labelledby': 'basic-button',
                }}
              >
                {/* <MenuItem onClick={handleClose}>Profile</MenuItem>
                <MenuItem onClick={handleClose}>My account</MenuItem> */}
                <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                <ListItemIcon>
                  <PersonIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>{user?.name || "Name"}</ListItemText>
                </MenuItem>
                <Divider />
                <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                <ListItemIcon>
                  <EmailIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>{user?.email || "email@email.com"}</ListItemText>
                </MenuItem>
                <Divider />
                <MenuItem sx={{py:0}} onClick={()=>{handleClose()}}>
                <ListItemIcon>
                  <PersonIcon fontSize="small" />
                </ListItemIcon>
                <ListItemText>{user?.role || 'role'}</ListItemText>
                </MenuItem>
                <Divider />
                <MenuItem sx={{py:0}} onClick={()=>{handleClose();logout()}}>
                <ListItemIcon>
                  <Logout fontSize="small" />
                </ListItemIcon>
                <ListItemText>Logout</ListItemText>
                </MenuItem>
                {/* <Divider /> */}
              </Menu>
             
              </div>
            </Stack>
          </Toolbar>
        </AppBar>
        <Box sx={{ maxWidth: `calc(100vw - ${open ? 180 : 50}px)` }}>
          <Routes />
        </Box>
      </Box>
    </div>
    {/* )} */}
    </>
          
  )
};

export default NavigationLayout;
