import React, { useEffect, useState } from "react";
import Modal from "../../components/modal";
import {
  SCHEMA_TYPES,
  VALIDATORS,
  TYPES,
  INV_TYPE,
  METER_TYPE,
  MODE,
  BYTE_SIZE,
  PARITY,
  STOP_BIT,
  OEM,
  BAUDRATE,
  HAVELLS_MAKE,
  SOLAREDGE_MAKE,
  SELEC_MAKE,
  SCHNEIDER_MAKE
} from "../../helpers/constants";
import { Stack, Grid } from "@mui/material";
import Button from "../../components/button";
import { Validate } from "../../helpers/utils";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

const AddAsset = ({
  open,
  onClose,
  data,
  setData,
  onSubmit,
  isTool,
  CheckedControl
}) => {
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(new Map());
  const [loading, setLoading] = useState(false);
  const [checked, setChecked] = useState(false);
  const [checkedControl, setCheckedControl] = useState(false);
  const [oemList, setOemList] = useState([])
  const [makeList, setMakeList] = useState([])
  const oemlist = JSON.parse(localStorage.getItem("oem"))

  useEffect(() => {
    console.log("rowdata for monitor---->>>", data)
    if (data.monitor === 1) {
      setChecked(true)
    } else {
      setChecked(false)
    }
    if (data.control === 1) {
      setCheckedControl(true)
    } else {
      setCheckedControl(false)
    }
  }, [data.monitor])

  useEffect(() => {
    newOem()
  }, []);

  useEffect(() => {
    setMakeList([])
    handleMake()
  }, [data.oem]);

  function newOem() {
    const keys = Object.keys(oemlist);
    keys.forEach(item => {
      if (item !== "") {
        let obj = { value: item, key: item };
        setOemList(prevList => [...prevList, obj]);
      }
    });
  }

  function handleMake() {
    if (data.oem) {
      oemlist[data.oem].forEach(item => {
        if (item !== "") {
          let obj = { value: item, key: item };
          setMakeList(prevList => [...prevList, obj]);
        }
      });
    }
  }

  const TOOL_SCHEMA = [
    {
      title: "Type",
      placeholder: "Select Type",
      type: SCHEMA_TYPES.SELECT,
      name: "type",
      validator: VALIDATORS.REQUIRED,
      keyValues: TYPES
    },
    {
      title: "Endpoint",
      placeholder: "Select Sub Type",
      type: SCHEMA_TYPES.SELECT,
      name: "sub_type",
      validator: VALIDATORS.REQUIRED,
      keyValues: data.type === "inv" ? INV_TYPE : METER_TYPE
    },
    {
      title: "OEM",
      placeholder: "Select OEM",
      type: SCHEMA_TYPES.SELECT,
      name: "oem",
      validator: VALIDATORS.REQUIRED,
      keyValues: oemList
    },
    {
      title: "Make",
      placeholder: "Select Make",
      type: SCHEMA_TYPES.SELECT,
      name: "make",
      validator: VALIDATORS.REQUIRED,
      keyValues: makeList
      // keyValues: data.oem==="Havells"?HAVELLS_MAKE:data.oem==="SolarEdge"?SOLAREDGE_MAKE:data.oem==="Selec"?SELEC_MAKE:data.oem==="Schneider"?SCHNEIDER_MAKE:null
    },
    {
      title: "Port",
      placeholder: "Select Mode",
      type: SCHEMA_TYPES.SELECT,
      name: "mode",
      validator: VALIDATORS.REQUIRED,
      keyValues: MODE
    },

  ];

  const TCP = [
    {
      title: "Slave IP",
      placeholder: "Enter IP",
      type: SCHEMA_TYPES.INPUT,
      name: "slave_ip",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Slave PORT",
      placeholder: "Enter PORT",
      type: SCHEMA_TYPES.INPUT,
      name: "slave_port",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Slave ID",
      placeholder: "Enter ID",
      type: SCHEMA_TYPES.INPUT,
      name: "slave_id",
      validator: VALIDATORS.REQUIRED,
    }
  ]

  const RS_485 = [
    {
      title: "Baudrate",
      placeholder: "Enter baudrate",
      type: SCHEMA_TYPES.SELECT,
      name: "baudrate",
      validator: VALIDATORS.REQUIRED,
      keyValues: BAUDRATE
    },
    {
      title: "Slave ID",
      placeholder: "Enter Slave ID",
      type: SCHEMA_TYPES.INPUT,
      name: "slave_id",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Byte size",
      placeholder: "Enter Byte size",
      type: SCHEMA_TYPES.SELECT,
      name: "byte_size",
      validator: VALIDATORS.REQUIRED,
      keyValues: BYTE_SIZE
    },
    {
      title: "Parity",
      placeholder: "Enter Parity",
      type: SCHEMA_TYPES.SELECT,
      name: "parity",
      validator: VALIDATORS.REQUIRED,
      keyValues: PARITY
    },
    {
      title: "Stop Bit",
      placeholder: "Enter stop bit",
      type: SCHEMA_TYPES.SELECT,
      name: "stop_bit",
      validator: VALIDATORS.REQUIRED,
      keyValues: STOP_BIT
    },

  ]

  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(TOOL_SCHEMA, data);
      setErrors(_errors);
      if (_errors.size) return;
      setLoading(true);
      await onCreate();
      setLoading(false);
      setStep(0);
    }
  };

  const onCreate = async () => {
    const tempAsset = {
      "type": data.type,
      "sub_type": data.sub_type,
      "oem": data.oem,
      "make": data.make,
      "mode": data.mode,
      "baudrate": data.baudrate,
      "BPS": data.byte_size + data.parity + data.stop_bit || "",
      "slave_id": data.slave_id,
      "slave_ip": data.slave_ip,
      "slave_port": data.slave_port,
      "monitor": checked === true ? 1 : 0,
      "control": checkedControl === true ? 1 : 0
    }
    await onSubmit(tempAsset)
    // console.log("final data",tempAsset)
  }
  const cleanup = () => {
    setErrors(new Map());
    setData({});
    setStep(0);
    onClose();
  };

  const handleBack = () => {
    if (step === 0) {
      cleanup();
    } else {
      setStep((prev) => prev - 1);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleMonitor = (event) => {
    setChecked(event.target.checked);
  };
  const handleControl = (event) => {
    setCheckedControl(event.target.checked);
  };

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(700px,80vw)"
    >
      <h3 className="opacity-80">CONFIG YOUR MACHINE</h3>
      <Grid display={'grid'} sx={{ gap: 1 }} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={TOOL_SCHEMA}
        />
        {data.mode === "tcp" && (
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={TCP}
          />
        )}
        {data.mode && data.mode !== "tcp" ? (
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={RS_485}
          />
        ) : null}
        <FormControlLabel control={
          <Checkbox
            checked={checked}
            onChange={handleMonitor}
            inputProps={{ 'aria-label': 'controlled' }}
          />
        }
          label="Monitor"
        />
        <FormControlLabel control={
          <Checkbox
            checked={checkedControl}
            onChange={handleControl}
            inputProps={{ 'aria-label': 'controlled' }}
          />
        }
          label="Control"
        />
      </Grid>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          Cancel
        </Button>
        <Button onClick={handleNext} loading={loading}>
          Next
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddAsset;
