import React, { useEffect, useState,useLayoutEffect } from "react";
import { useLocation } from "react-router-dom";
import { Stack, Typography, IconButton } from "@mui/material";
import Table from "../../components/table";
import Button from "../../components/button";
import AddAsset from "./AddAsset";
import AddFeature from './AddFeature'
import { toast } from "react-toastify";
import useAsset from "../../store/asset";
import { COLORS } from "../../helpers/constants";
import CancelIcon from '@mui/icons-material/Cancel';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';

const ConfigAsset = () => {
  const { create, assetsList, clientAttribute } = useAsset()
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [edit, setEdit] = useState(false)
  const [data, setData] = useState({});
  const [assets, setAssets] = useState([])
  const [deviceVersion, setDeviceVersion] = useState(1)
  const deviceName = localStorage.getItem("deviceName")
  const [feature, setFeature] = useState({})
  const [same, setSame] = useState(false)
  const COLUMNS = [
    {
      id: 42,
      title: "Type",
      render: (rowData) => rowData.type,
    },
    {
      id: 1,
      title: "Sub Type",
      render: (rowData) => rowData.sub_type,
    },
    {
      id: 2,
      title: "OEM",
      render: (rowData) => rowData.oem,
    },
    {
      id: 3,
      title: "Make",
      render: (rowData) => rowData.make,
    },
    {
      id: 4,
      title: "Mode",
      render: (rowData) => rowData.mode,
    },
    {
      id: 5,
      title: "Slave IP",
      render: (rowData) => rowData?.slave_ip,
    },
    {
      id: 6,
      title: "Slave PORT",
      render: (rowData) => rowData.slave_port
    },
    {
      id: 7,
      title: "Slave ID",
      render: (rowData) => rowData.slave_id,
    },
    {
      id: 8,
      title: "Baudrate",
      render: (rowData) => rowData?.baudrate
    },
    {
      id: 9,
      title: "BPS",
      render: (rowData) => rowData?.BPS
    },
    {
      id: 10,
      title: "Monitor",
      render: (rowData) => rowData?.monitor === 1 ? <CheckCircleIcon color="success" /> : <CancelIcon color="error" />
    },
    {
      id: 11,
      title: "Control",
      render: (rowData) => rowData?.control === 1 ? <CheckCircleIcon color="success" /> : <CancelIcon color="error" />
    },
    {
      id: 12,
      title: "Action",
      render: (rowData) => <Stack sx={{ flexDirection: 'row' }}>
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => handleDelete(rowData)}
        >
          <DeleteIcon color="error" fontSize="small" />
        </IconButton>
        <IconButton
          sx={{ height: 25, width: 25 }}
          onClick={() => {
            setModalOpen("add");
            if (rowData.mode !== "tcp") {
              setData({ ...rowData, "byte_size": rowData.BPS[0], "parity": rowData.BPS[1], "stop_bit": rowData.BPS[2] })
            } else {
              setData(rowData)
            }
          }}
        >
          <EditIcon color="primary" fontSize="small" />
        </IconButton>
      </Stack>
    }
  ];

  useEffect(() => {
      fetchAssets()
  }, [deviceName])

  async function fetchAssets() {
    const ast = await assetsList()
    const deviceConfigData = ast[0].find(item => item.key === "device_config");
    const sharedAtt = await clientAttribute()
    const clientConfigData = sharedAtt[0].find(item => item.key === "device_config");
    if (clientConfigData) {
      setSame(JSON.stringify(deviceConfigData.value) === JSON.stringify(clientConfigData.value))
    } else {
      setSame(false)
    }
    // console.log("deviceConfigData----------",deviceConfigData)
    if(deviceConfigData){
      setAssets(deviceConfigData.value.assets)
      setFeature(deviceConfigData.value.features)
    }else{
      setAssets([])
      setFeature({})
    }
    
    const deviceConfigVersion = ast[0].find(item => item.key === "config_version");
    if (deviceConfigVersion) {
      // console.log("device version-----",deviceConfigVersion.value)
      setDeviceVersion(deviceConfigVersion.value)
    } else {
      setDeviceVersion(1)
    }
  }

  const handleDelete = (val) => {
    let abc = assets.indexOf(val)
    const updatedItems = [...assets];
    updatedItems.splice(abc, 1);
    setAssets(updatedItems)
  }

  const handleSubmit = async (abc) => {
    setAssets([...assets, abc])
    setModalOpen(false)
  };

  const exportData = async (feature, val) => {
    const configFile =
    {
      "gridx_id": deviceName,
      "version": deviceVersion + 1,
      "assets": assets,
      "features": feature
    }
    setModalOpen(false)

    if (val === "upload") {
      const res = await create({ "device_config": configFile, "config_version": deviceVersion + 1 })
      //  console.log("response----upload version--",res)
      if (res.status === 200) {
        fetchAssets()
      }
    } else {
      downloadFile(configFile)
    }
  };

  async function downloadFile(configFile) {
    const jsonString = `data:text/json;chatset=utf-8,${encodeURIComponent(
      JSON.stringify(configFile, null, 4)
    )}`;
    const link = document.createElement("a");
    link.href = jsonString;
    link.download = "config_File.json";
    link.click()
  }

  return (
    <div className="mx-3 my-2">
      <Stack sx={{ justifyContent: 'space-between', flexDirection: "row" }}>
        <Stack sx={{ flexDirection: 'row', gap: 1 }}>
          <Typography color={COLORS.PRIMARY}>{deviceName}</Typography>
          <Typography color={COLORS.SUCCESS}>Config Version : {deviceVersion}</Typography>
        </Stack>
        {same === true ? (
          <Typography color={COLORS.SUCCESS}>Configuration File Successfully Uploaded</Typography>
        ) : (
          <Typography color={COLORS.ERROR}>Configuration File Mismatch</Typography>
        )}
      </Stack>
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Config Assets
          <span className="fs-30 ml-1 c-gray">({assets.length})</span>
        </h1>
        <Stack justifyContent="flex-end" direction="row">
          <Button onClick={() => setModalOpen("add")}>Create Asset</Button>
        </Stack>
      </div>
      <Table
        edit={edit}
        columns={COLUMNS}
        rows={assets}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-500"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddAsset
        onClose={() => setModalOpen(false)}
        open={modalOpen === "add"}
        data={data}
        onSubmit={handleSubmit}
        setData={setData}
      />
      <AddFeature
        onClose={() => setModalOpen(false)}
        open={modalOpen === "addFeature"}
        data={data}
        onSubmit={exportData}
        setData={setData}
        feature={modalOpen === "addFeature" ? feature : null}
      />
      <Stack justifyContent="flex-end" direction="row" mt={1}>
        <Button onClick={() => setModalOpen("addFeature")}>Save Feature List</Button>
      </Stack>
    </div>
  );
};

export default ConfigAsset;
