import { useEffect, useState } from "react";
import styles from "./input.module.scss";
import { Chip, Box, Autocomplete, TextField } from "@mui/material";
import cx from "classnames";
import ArrowDropDown from "@mui/icons-material/ArrowDropDown";
import { createEvent } from "../../helpers/utils";
import Spin from "../spin";
import empty from "../../assets/png/empty.png";
import { COLORS } from "../../helpers/constants";
const DEBOUNCE_DELAY = 500;
let debounceTimer;

const SearchableSelect = ({
  hide,
  value,
  multi,
  name,
  keyValues = [],
  placeholder,
  onChange,
  top,
  error,
  height,
  width,
  title,
  classes,
  required,
  gray,
  disabled,
  fetch = [Function.prototype, ""],
  limit = 10,
  noSearch,
}) => {
  const [loading, setLoading] = useState(false);
  const [options, setOptions] = useState(keyValues);
  const [inputValue, setInputValue] = useState(value);

  const handleSelectionChange = (e, newValue) => {
    if (!newValue) return;
    if (multi && newValue?.length > limit) return;
    onChange(createEvent(name, multi ? newValue : newValue || ""));
  };

  const fetchOptions = async () => {
    const [_fetch, args] = fetch;
    if (!_fetch) return;
    setLoading(true);
    const res = await _fetch(
      args && Object.entries(args)?.length ? args : inputValue
    );
    setLoading(false);
    setOptions(res?.length ? res : []);
  };

  const fetchOptionsDebounced = () => {
    clearTimeout(debounceTimer);
    debounceTimer = setTimeout(() => {
      fetchOptions();
    }, DEBOUNCE_DELAY);
  };

  useEffect(() => {
    if (!noSearch) fetchOptionsDebounced();
  }, [inputValue]);

  if (hide) return null;

  return (
    <div
      data-testid="select"
      style={{ marginTop: top }}
      className={cx(styles.inputContainer, classes)}
    >
      {!!title && (
        <p className={styles.title}>
          {title}
          {!!required && <span>*</span>}
        </p>
      )}
      <Autocomplete
        multiple={Boolean(multi)}
        options={
          multi
            ? options?.filter(
                (option) =>
                  !value?.some(
                    (selectedOption) => selectedOption?.key === option?.key
                  )
              )
            : options
        }
        className={cx(
          styles.inputContainer,
          styles.select,
          { [styles.error]: !!error },
          classes
        )}
        getOptionLabel={(option) => option?.value || ""}
        value={value}
        loading={loading}
        placeholder={placeholder}
        onChange={handleSelectionChange}
        onOpen={fetchOptions}
        popupIcon={!value?.length ? <ArrowDropDown /> : null}
        loadingText={
          <Box className="my-10 h-140 flex items-center justify-center w-full">
            <Spin big blue />
          </Box>
        }
        sx={{
          height: !!height && height,
          width: !!width && width,
          background: !!gray && "#f5f5f5",
          "& .MuiInputBase-root": {
            maxHeight: "110px",
            paddingLeft: "5px",
            overflowY: "auto",
            fontSize: "14px",
            "::-webkit-scrollbar": {
              display: "none",
            },
            "-ms-overflow-style": "none",
            "scrollbar-width": "none",
          },
        }}
        renderTags={(_comp, props) =>
          value?.map((option, index) => (
            <Chip
              label={option?.value}
              onDelete={props({ index }).onDelete}
              key={option.key}
              sx={{
                height: "24px",
                fontSize: "0.75rem",
                margin: "1px",
                "& .css-i4bv87-MuiSvgIcon-root": {
                  border: "none!important",
                },
              }}
            />
          ))
        }
        noOptionsText={
          options.length === 0 && (
            <Box className="my-10 w-full flex flex-column items-center justify-center text-center">
              <img src={empty} height="100px" width="auto" alt="empty" />
              <p className="mt-3 fs-14 opacity-30 tracking-wide">
                No data found!
              </p>
            </Box>
          )
        }
        renderInput={(params) => (
          <TextField
            {...params}
            value={inputValue}
            onChange={(e) => {
              setInputValue(e.target.value);
              options.push({
                key: e.target.value,
                value: e.target.value,
                isNew: true,
              });
              onChange(
                createEvent(name, {
                  key: e.target.value,
                  value: e.target.value,
                  isNew: true,
                })
              );
            }}
            variant="outlined"
            InputProps={{
              ...params.InputProps,
              endAdornment: <>{params.InputProps.endAdornment}</>,
            }}
            sx={{
              "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline, & .MuiOutlinedInput-root:hover .MuiOutlinedInput-notchedOutline, & .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline ":
                {
                  border: error ? `1px solid ${COLORS.RED}` : "1px solid #eee",
                },
              ".MuiAutocomplete-inputRoot": {
                padding: "0px 15px 0px 0px!important",
                background: "#fff",
                minHeight: "40px!important",
              },
              "& .MuiAutocomplete-clearIndicator": {
                visibility: "visible !important",
                color: "#000000 !important",
              },
              "& .css-1glvl0p-MuiButtonBase-root-MuiIconButton-root-MuiAutocomplete-clearIndicator ":
                {
                  padding: multi && value && "0px",
                },
            }}
          />
        )}
        renderOption={(props, option) => (
          <div component="li" {...props}>
            {option?.value}
          </div>
        )}
      />
      {error && <p className={styles.error}>{error}</p>}
    </div>
  );
};

export default SearchableSelect;
