import React, { useEffect, useState } from "react";
import Modal from "../../components/modal";
import {
  SCHEMA_TYPES,
  VALIDATORS,
  TYPES,
  INV_TYPE,
  METER_TYPE,
  MODE,
  BYTE_SIZE,
  PARITY,
  STOP_BIT,

} from "../../helpers/constants";
import { Step, StepLabel, Stepper } from "@material-ui/core";
import { Stack, Grid } from "@mui/material";
import Button from "../../components/button";
import { Validate } from "../../helpers/utils";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';

const AddFeature = ({
  open,
  onClose,
  data,
  setData,
  onSubmit,
  isTool,
  feature
}) => {
  const [step, setStep] = useState(0);
  const [errors, setErrors] = useState(new Map());
  const [errors2, setErrors2] = useState(new Map());
  const [loading, setLoading] = useState(false);
  const [dg_checked, setDg_checked] = useState(false);
  const [export_control, setExport_control] = useState(false)
  const [export_allow, setExport_allow] = useState(false)
  const [monitor_only, setMonitor_only] = useState(false)

  useEffect(() => {
    if (feature !== null) {
      if (feature.dg_sync) {
        if (feature.dg_sync["enable"] === 1) {
          setDg_checked(true)
        } else {
          setDg_checked(false)
        }
        if (feature.export_control.enable === 1) {
          setExport_control(true)
        } else {
          setExport_control(false)
        }
        if (feature.export_allow.enable === 1) {
          setExport_allow(true)
        } else {
          setExport_allow(false)
        }
        setData({
          ...data, "dg_capacity": feature.dg_sync.capacity, "dg_threshold": feature.dg_sync.threshold,
          "control_sanction_load": feature.export_control.sanction_load, "control_threshold": feature.export_control.threshold
        })
      }
    }
  }, [feature])

  const DG = [
    {
      title: "DG Capacity",
      placeholder: "Enter DG Capacity",
      type: SCHEMA_TYPES.INPUT,
      name: "dg_capacity",
      validator: VALIDATORS.REQUIRED,
      inputType: "number"
    },
    {
      title: "DG Threshold",
      placeholder: "Enter DG Threshold",
      type: SCHEMA_TYPES.INPUT,
      name: "dg_threshold",
      validator: VALIDATORS.REQUIRED,
      inputType: "number"
    }
  ];

  const EXPORT_CONTROL = [
    {
      title: "Control Sanction Load",
      placeholder: "Enter Control Sanction Load",
      type: SCHEMA_TYPES.INPUT,
      name: "control_sanction_load",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Control Threshold",
      placeholder: "Enter Control Threshold",
      type: SCHEMA_TYPES.INPUT,
      name: "control_threshold",
      validator: VALIDATORS.REQUIRED,
    }
  ]

  const handleNext = async (val) => {
    const _errors = Validate(EXPORT_CONTROL, data);
    const _errorss = Validate(DG, data);
    if (dg_checked === true) {
      setErrors2(_errorss)
      if (_errorss.size) return;
    } else {
      setErrors2(new Map())
    }
    if (export_control === true) {
      setErrors(_errors)
      if (_errors.size) return;
    } else {
      setErrors(new Map())
    }
    setLoading(true);
    await onCreate(val);
    setLoading(false)
  }

  const onCreate = async (val) => {
    const tempFeature = {
      "dg_sync": {
        "enable": dg_checked ? 1 : 0,
        "capacity": dg_checked ? parseInt(data.dg_capacity) : '',
        "threshold": dg_checked ? parseInt(data.dg_threshold) : ''
      },
      "export_control": {
        "enable": export_control ? 1 : 0,
        "sanction_load": export_control ? parseInt(data.control_sanction_load) : '',
        "threshold": export_control ? parseInt(data.control_threshold) : ''
      },
      "export_allow": {
        "enable": export_allow ? 1 : 0
      },
      "monitor_only": {
        "enable": monitor_only ? 1 : 0
      }
    }
    await onSubmit(tempFeature, val)
  }

  const cleanup = () => {
    setErrors(new Map());
    setData({ location: {}, specification: {} });
    setStep(0);
    onClose();
  };

  const handleBack = () => {
    if (step === 0) {
      cleanup();
    } else {
      setStep((prev) => prev - 1);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleDgSync = (e) => {
    if(!monitor_only){
      setDg_checked(e.target.checked);
    }
  };
  const handleExportControl = (e) => {
    if(!monitor_only){
    if (e.target.checked) { setExport_allow(false) }
    setExport_control(e.target.checked);
    }
  };

  const handleExportAllow = (e) => {
    if(!monitor_only){
    if (e.target.checked) { setExport_control(false) }
    setExport_allow(e.target.checked)
    }
  }
  const handleMonitorOnly = (e) => {
    if (e.target.checked) { setExport_control(false);setExport_allow(false);setDg_checked(false) }
    setMonitor_only(e.target.checked)
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      wrapperClasses="px-5 pt-0 pb-4"
      width="min(700px,80vw)"
    >
      <h3 className="opacity-80">Add Feature</h3>
      <Grid display={'grid'} sx={{ gap: 2 }} gridTemplateColumns={{ sm: 'repeat(4, 1fr)', xs: 'repeat(4, 1fr)' }}>
      <Stack>
          <FormControlLabel control={
            <Checkbox
              checked={monitor_only}
              onChange={handleMonitorOnly}
              inputProps={{ 'aria-label': 'controlled' }}
            />
          }
            label="Monitor Only"
          />
          {/* {export_allow && (<SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={EXPORT_ALLOW}
        />
        )} */}
        </Stack>
        <Stack>
          <FormControlLabel control={
            <Checkbox
              checked={dg_checked}
              onChange={handleDgSync}
              inputProps={{ 'aria-label': 'controlled' }}
            />
          }
            label="DG Sync"
          />
          {dg_checked && (<SchemaExtractor
            data={data}
            errors={errors2}
            onChange={handleChange}
            schema={DG}
          />
          )}
        </Stack>
        <Stack>
          <FormControlLabel control={
            <Checkbox
              checked={export_control}
              onChange={handleExportControl}
              inputProps={{ 'aria-label': 'controlled' }}
            />
          }
            label="Export Control"
          />
          {export_control && (<SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={EXPORT_CONTROL}
          />
          )}
        </Stack>
        <Stack>
          <FormControlLabel control={
            <Checkbox
              checked={export_allow}
              onChange={handleExportAllow}
              inputProps={{ 'aria-label': 'controlled' }}
            />
          }
            label="Export Allow"
          />
          {/* {export_allow && (<SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={EXPORT_ALLOW}
        />
        )} */}
        </Stack>
       
      </Grid>

      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={handleBack}>
          Cancel
        </Button>
        <Stack
          direction="row"
          alignItems="center"
          justifyContent="space-between"
          gap={1}>
          <Button onClick={handleNext} loading={loading}>
            Download File
          </Button>
          <Button onClick={() => handleNext('upload')} loading={loading}>
            Upload File
          </Button>
        </Stack>
      </Stack>
    </Modal>
  );
};

export default AddFeature;
