import React, { useEffect, useState } from "react";
import { Route, Routes as AllRoutes } from "react-router-dom";
import {ROLE} from "./helpers/constants"
import { useDispatch, useSelector } from "react-redux";
import { REDUCER_TYPES } from "./store";
import { resetAuth } from "./store/api/utils";
import ConfigAsset from "./pages/store/ConfigAsset";
import Error from "./pages/error";

const Routes = () => {

  const dispatch = useDispatch();
  const [hydrated, setHydrated] = useState(false);
  const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
 const role = state?.user?.data?.role
  const routes = [ 
    {
      path: "/config-machine",
      element: <ConfigAsset />,
      // element: role === ROLE.ADMINISTRATOR?<ConfigAsset />:<Error/>,
    },
    {
      path: "/error",
      element: <Error/>,
    }, 
   

  ];
  useEffect(() => {
    resetAuth(dispatch, state);
    setHydrated(true);
  }, []);

  if (!hydrated) {
    return null;
  }
  return (
    <AllRoutes>
      {routes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
    </AllRoutes>
   
  );
};

export default Routes;
